import React, {useContext, useState} from 'react'
import {useHistory} from "react-router-dom";
import {GlobalContext} from "../../context/GlobalState";
import {StyledButtonBox} from "../atoms/ButtonBox";
import {Button, StyledButton} from "../atoms/Button";
import styled from "styled-components";
import {Input, StyledInput} from "../atoms/Input";


const SearchBar = styled.div`
  position: relative;
  //margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  & > :not(:last-child){
    margin-right: 15px;
  }

  @media (max-width:650px) {
    flex-direction: column;
    margin: 0 0 0 0;

    & > * {
      width: 100%;
    }
    & > :not(:last-child) {
      margin-bottom: 15px;
    }
  }
`

const SearchBox = styled.div`
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  & > :first-child{
    margin-right: 15px;
  }
  
  @media (max-width:450px) {
    flex-direction: column;
    margin: 0 0 0 0;

    & > * {
      width: 100%;
    }
    & > :not(:last-child) {
      margin-bottom: 15px;
    }
`
const AddBtn = styled(StyledButtonBox)`
  justify-content: flex-start;
  //padding-left: 20px;
`

const ButtonClear = styled(StyledButton)`
  /*margin-left: 15px;*/
  //padding-right: 20px;
`

const SearchInput = styled(Input)`
  padding-right: 10px ;
  padding-left: 10px;
`


export const Search = () => {
    const [search, setSearch] = useState('')
    const {searchAnnounces, cleanSearchResult} = useContext(GlobalContext)
    const history = useHistory()

    const addAnnounce = () => {
        history.push(`/newAnnouncement`)
    }

    const onSearchChange = ({target}) => {
        const {value} = target
        setSearch(value)
        if (!value) {
            cleanSearchResult()
            return
        }
        searchAnnounces(value)
    }

    const clearSearch = () => {
        setSearch('')
        cleanSearchResult()
    }

    return (
        <SearchBar>
            {/*<AddBtn>*/}
                {/*<ButtonBox>*/}
                <Button onClick={() => addAnnounce()}>+ New message</Button>
                {/*</ButtonBox>*/}
                <SearchBox>
                    {/*<SearchStyle>*/}
                    <SearchInput type="search" value={search} onChange={onSearchChange} placeholder={'Search'}/>
                    {/*</SearchStyle>*/}
                    <Button onClick={clearSearch}>Clear</Button>
                </SearchBox>
            {/*</AddBtn>*/}


        </SearchBar>
    )
}